import React, { useState, useEffect } from "react";
import logo from "./logo.svg";
import "./App.css";

import Login from "./Login";
import NewService from "./NewService";
import { Switch, Route, Redirect, Link, useHistory } from "react-router-dom";
import { getServices, subscribe, unsubscribe } from "./services";

function App() {
  /*
  if (!window.localStorage.email) {
    return <Login />;
  }
  */
  return (
    <Switch>
      <Route exact path="/login" component={Login}>
        <Login />
      </Route>
      <Route
        exact
        path="/new-service"
        render={() =>
          window.localStorage.email ? (
            <NewService />
          ) : (
            <Redirect
              to={{ path: "/login", state: { from: "/new-service" } }}
            />
          )
        }
      ></Route>
      <Route
        render={() =>
          window.localStorage.email ? <Main /> : <Redirect to="/login" />
        }
      ></Route>
    </Switch>
  );
}

const Main = ({ location }) => {
  const [showOnlySubscribed, setShowOnlySubscribed] = useState(false);
  const [allData, setAllData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [subscribeLoading, setSubscribeLoading] = useState("");
  const [error, setError] = useState("");

  const history = useHistory();

  useEffect(() => {
    const fetchServices = async () => {
      try {
        let services = await getServices();
        setAllData(services);
        setLoading(false);
        console.log(services);
      } catch (err) {
        setError(err.toString());
        setLoading(false);
      }
    };

    fetchServices();
  }, []);

  const handleChange = (event) => {
    setShowOnlySubscribed(event.target.checked);
    console.log(event.target.checked);
  };

  const handleSubscribed = (currentSubscriptionStatus, name) => async () => {
    console.log(currentSubscriptionStatus, name);
    setSubscribeLoading(name);
    try {
      if (currentSubscriptionStatus) {
        await unsubscribe(name);
      } else {
        await subscribe(name);
      }
      setAllData(
        allData.map((data) =>
          data.name === name
            ? { ...data, subscribed: !currentSubscriptionStatus }
            : data
        )
      );
      setSubscribeLoading("");
      setError("");
    } catch (err) {
      setSubscribeLoading("");
      setError(err);
      console.info(err);
    }
  };

  const handleLogout = () => {
    window.localStorage.clear();
    history.push("/login");
  };

  return (
    <div>
      <p>
        Welcome {window.localStorage.email}!
        <button className="btn btn-link text-danger" onClick={handleLogout}>
          Logout
        </button>
      </p>

      {error && <div className="alert alert-danger">{error}</div>}
      {loading ? (
        <div className="text-center">
          <div className="spinner-border" role="status">
            <span className="sr-only">Loading...</span>
          </div>
        </div>
      ) : (
        <table className="card px-3 shadow rounded my-3 table table-responsive">
          <caption>
            <span className="mr-2">Show only subscribed services</span>
            <input
              type="checkbox"
              name="serviceDisplayed"
              id="serviceDisplayed"
              onChange={handleChange}
            />
          </caption>
          <tbody>
            {allData.map((data) => (
              <>
                {((showOnlySubscribed && data.subscribed) ||
                  !showOnlySubscribed) && (
                  <tr key={data.name}>
                    <td className="align-middle text-capitalize">
                      {data.name}
                    </td>
                    <td className="align-middle">{data.url}</td>
                    <td
                      className={`align-middle ${
                        data.status ? "text-success" : "text-danger"
                      }`}
                    >
                      {data.status ? "Up" : "Down"}
                      <span
                        className={`ml-3 fa fa-arrow-${
                          data.status ? "up" : "down"
                        }`}
                      />
                    </td>
                    <td className="align-middle">
                      <button
                        onClick={handleSubscribed(data.subscribed, data.name)}
                        className={`btn btn-link ${
                          data.subscribed ? "text-primary" : "text-info"
                        }`}
                      >
                        {data.subscribed ? "Unsubscribe" : "Subscribe"}
                        {subscribeLoading === data.name && (
                          <span
                            className="spinner-border spinner-border-sm ml-2"
                            role="status"
                            aria-hidden="true"
                          />
                        )}{" "}
                      </button>
                    </td>
                  </tr>
                )}
              </>
            ))}
          </tbody>
        </table>
      )}
      <Link to="/new-service">Add new service</Link>
    </div>
  );
};
export default App;
