const BASE_URL = "https://api-healthcheck.herokuapp.com/api/v1";

const getServices = async () => {
  let res = await fetch(`${BASE_URL}/services`);
  const allServices = await res.json();

  res = await fetch(
    `${BASE_URL}/services?subscriber=${window.localStorage.email}`
  );
  let subscribedServices = await res.json();
  subscribedServices = new Set(
    subscribedServices.map((service) => service.name)
  );

  return allServices.map((service) => ({
    ...service,
    subscribed: subscribedServices.has(service.name),
  }));
};

const addService = async (name, url, toSubscribe) => {
  let res = await fetch(`${BASE_URL}/services`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ name, url }),
  });

  if (!res.ok) {
    res = await res.json();
    return Promise.reject(res.message);
  }

  if (toSubscribe) {
    await subscribe(name);
  }
};

const subscribe = async (name) => {
  await fetch(`${BASE_URL}/subscribe`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ name, email: window.localStorage.email }),
  });
};

const unsubscribe = async (name) => {
  await fetch(`${BASE_URL}/unsubscribe`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ name, email: window.localStorage.email }),
  });
};

export { getServices, subscribe, unsubscribe, addService };
