import React, { useState, useRef } from "react";
import { useHistory, Link } from "react-router-dom";

import { addService } from "./services";

const NewService = () => {
  let name, url, subscribe;
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  const history = useHistory();
  const urlInput = useRef();
  const handleNameChange = (event) => {
    name = event.target.value;
  };
  const handleUrlChange = (event) => {
    url = event.target.value;
    console.log(urlInput.current.validity.patternMismatch);
    if (urlInput.current.validity.patternMismatch) {
      urlInput.current.setCustomValidity(
        "Enter a valid URL starting with http:// or https://"
      );
    } else {
      urlInput.current.setCustomValidity("");
    }
  };
  const handleSubscribeChange = (event) => {
    subscribe = event.target.checked;
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      setLoading(true);
      console.log(name, url, subscribe);
      await addService(name, url, !!subscribe);
      history.push("/");
      setLoading(false);
    } catch (err) {
      setLoading(false);
      setError(err);
    }
  };

  return (
    <div>
      {error && <div className="alert alert-danger">{error}</div>}
      <Link to="/">
        <span className="fa fa-chevron-left mr-2" />
        Back to services
      </Link>
      <form onSubmit={handleSubmit} className="mt-3">
        <div className="form-group">
          <label htmlFor="serviceName">Name of the service</label>
          <input
            id="serviceName"
            type="text"
            onChange={handleNameChange}
            className="form-control"
            required
          />
        </div>
        <div className="form-group">
          <label htmlFor="serviceUrl">Url of the service</label>
          <input
            id="serviceUrl"
            type="url"
            pattern="https?://.+"
            onChange={handleUrlChange}
            className="form-control"
            ref={urlInput}
            required
          />
        </div>
        <div className="form-check">
          <input
            id="serviceSubscribe"
            type="checkbox"
            onChange={handleSubscribeChange}
            className="form-check-input"
          />
          <label htmlFor="serviceSubscribe" className="form-check-label">
            Subscribe to the service
          </label>
        </div>
        <button type="submit" className="btn btn-primary">
          Add
          {loading && (
            <span
              className="spinner-border spinner-border-sm ml-2"
              role="status"
              aria-hidden="true"
            />
          )}
        </button>
      </form>
    </div>
  );
};

export default NewService;
